'use strict';

import Calendar from 'react-calendar';
const React = require('react');
const root = '/api';
import axios from 'axios';

class FindPrescribtion extends React.Component {

    constructor(props) {
        super(props);
        this.state = {prescribtions: [], links: [], prescribtionUp: [], showOnUpdate: false,
        links2: this.props.location.state.links2,
            selfHrefHospitalSheetChart: this.props.location.state.selfHrefHospitalSheetChart, errors: []};
        this.onDelete = this.onDelete.bind(this);
        this.onUpdate = this.onUpdate.bind(this);
        this.onSaveUp = this.onSaveUp.bind(this);
        this.onStart = this.onStart.bind(this);
        this.onCreate = this.onCreate.bind(this);
        this.onClose = this.onClose.bind(this);

    }



    onDelete(hospitalSheetChart) {
        this.setState({
            errors: null
        });
        axios.delete(hospitalSheetChart._links.self.href).finally(()=> this.onStart());
        }

    componentDidMount() {
        this.onStart();
        }

    onStart() {
        axios.get(this.state.selfHrefHospitalSheetChart + "/prescribtions?", {params: {projection: "minimalFalse"}})
            .then(res => {
                const prescribtions = res.data;
                this.setState({
                    prescribtions: res.data._embedded.prescribtions,
                    links: res.data._links.self.href
                });
            });

    }

    onCreate(prescribtion) {
        var allErr = null;
        var allErrN = null;

        axios.post(this.state.selfHrefHospitalSheetChart + "/prescribtions", prescribtion)
            .then(()=> {
                this.setState({
                    errors: null
                });

            })
            .catch(error=> {
                    error.response.data.map(err => {
                        allErr = allErr + "; " + err.defaultMessage;
                        allErrN = allErr.substr(6, (allErr.length - 1));
                    });
                    this.setState({
                        errors: allErrN
                    });
                }
            )
            .finally(()=> this.onStart());

    }

    onClose() {
        this.setState({
            showOnUpdate: false
        });

    }

    onUpdate(prescribtion) {
        this.setState({
            prescribtionUp: prescribtion,
            showOnUpdate: true
        });
        }



    onSaveUp(enddate, endcomment, prescribtion){

        var allErr = null;
        var allErrN = null;

        prescribtion.enddate=enddate;
        prescribtion.endcomment=endcomment;
        prescribtion.b=false;

        axios.put(prescribtion._links.self.href, prescribtion)
            .then(()=> {
                this.setState({
                    errors: null
                });

            })
            .catch(error=> {
                    error.response.data.map(err => {
                        allErr = allErr + "; " + err.defaultMessage;
                        allErrN = allErr.substr(6, (allErr.length - 1));
                    });
                    this.setState({
                        errors: allErrN
                    });
                }
            )
            .finally(()=>this.onStart());

        this.setState({
            showOnUpdate: false
        });

    }

    render() {
        return (
            <div className="container justify-content-center">

                <div className="row justify-content-end">
                    <form name='f' action='/logout' method='POST'>
                        <button className="btn btn-outline-dark" type="submit" name="submit"> Logout </button>
                    </form>
                </div>

                <div className="row justify-content-center">
                    <div className="col-10">
                        <p>{this.state.errors}</p>
                    </div>
                </div>

                <PrescribtionList prescribtions={this.state.prescribtions}
                                        onDelete={this.onDelete}
                                        onUpdate={this.onUpdate}
                                        onSaveUp={this.onSaveUp}
                                        prescribtionUp={this.state.prescribtionUp}
                                        onOpen={this.onOpen}
                                        links={this.state.links}
                                        links2={this.state.links2}
                                        showOnUpdate={this.state.showOnUpdate}
                                        onCreate={this.onCreate}
                                        onClose={this.onClose}

                />
            </div>
        )
    }
}


class PrescribtionList extends React.Component{

    constructor(props) {
        super(props);

    }

    render() {
        let prescribtions = this.props.prescribtions.map(prescribtion =>
            <Prescribtion key={prescribtion._links.self.href} prescribtion={prescribtion}
                               onDelete={this.props.onDelete}
                               onUpdate={this.props.onUpdate}
                               onOpen={this.props.onOpen}
                                links2={this.props.links2}
            />
        );

        return (
            <div>

                <div className="row justify-content-center">
                    <CreatePrescribtion links={this.props.links} onCreate={this.props.onCreate}/>
                </div>

                <div className="row justify-content-center">
                <table className="table table-hover table-light">
                    <thead>
                    <tr className="d-flex">
                        <th className="col-1"> </th>
                        <th className="col-1">  </th>
                        <th className="col-1"> </th>
                        <th className="col-1"> </th>
                        <th className="col-1"></th>
                        <th className="col-1">  </th>
                        <th className="col-1"> </th>
                        <th className="col-1">  </th>
                        <th className="col-1"> </th>
                        <th className="col-1"> </th>

                    </tr>

                    </thead>

                    <tbody>
                    {prescribtions}
                    </tbody>

                </table>
                </div>

                <div className="row justify-content-center">

                <UpdatePrescribtion onSaveUp={this.props.onSaveUp}
                                          prescribtionUp={this.props.prescribtionUp}
                                          showOnUpdate={this.props.showOnUpdate}
                                            onClose={this.props.onClose}
                />
                </div>
            </div>
        )
    }
}


class CreatePrescribtion extends React.Component{

    constructor(props) {
        super(props);
        this.create = this.create.bind(this);
        this.changeShow = this.changeShow.bind(this);
        this.renderNull = this.renderNull.bind(this);
        this.renderUp = this.renderUp.bind(this);
        this.renderUpSt = this.renderUpSt.bind(this);
        this.changeRendUp = this.changeRendUp.bind(this);
        this.findAntibiotics = this.findAntibiotics.bind(this);
        this.onChangeSt = this.onChangeSt.bind(this);
        this.showSt = this.showSt.bind(this);
        this.state = {showOnCreate: false, antibiotics: [],
            showStateSt: false, startcomment: [],
            antibiotic: [], dosage: [], multiplicity: [] };

    }

    componentDidMount() {
        this.findAntibiotics();
    }

    changeShow(){
        this.setState({
            showOnCreate: !this.state.showOnCreate
        });
        this.setState({
            startcomment: "",
            antibiotic: "",
            dosage: "",
            multiplicity: ""
        });
    }

    findAntibiotics(){
        axios.get('http://localhost:8080/api/antibiotics')
            .then(res => {
                const antibiotics = res.data;
                this.setState({
                    antibiotics: antibiotics._embedded.antibiotics
                });

            });

    }
    create(e) {
        e.preventDefault();
        var a;
        let d = document.getElementById('select_antibiotic').value;
        a = d.substring(d.lastIndexOf('/') + 1);

        let startdate = this.refs.update_startdate2.value;
        let startcomment = this.refs.update_startcomment2.value;
        let dosage = this.refs.update_dosage2.value;
        let multiplicity = this.refs.update_multiplicity2.value;


        class Antibiotic {
            constructor(id) {
                this.id=id;
            }
        }

        class Prescribtion {
            constructor(startdate, startcomment, dosage, multiplicity, antibiotic, b) {
                this.startdate=startdate;
                this.startcomment=startcomment;
                this.dosage=dosage;
                this.multiplicity=multiplicity;
                this.antibiotic=antibiotic;
                this.b=b;
            }
        }

        this.props.onCreate(new Prescribtion(startdate, startcomment, dosage, multiplicity, new Antibiotic(a), true));
        this.changeShow();

        this.setState({
            startcomment: "",
            antibiotic: "",
            dosage: "",
            multiplicity: ""
        });
    }


    renderUp() {

        let antibiotics = this.state.antibiotics.map(antibiotic =>
            <option value = {antibiotic._links.self.href}>{antibiotic.name}</option>
        );

        return (
            <div className="col-6">

                <form ref="myForm" onChange={this.changeRendUp}>
                    <input type="text" ref="update_startdate2" onClick={this.showSt} placeholder=" " className="form-control" id="updateDateSt"/>

                    <input type="text" ref="update_startcomment2" value={this.state.startcomment} placeholder="  " className="form-control" id="startcomment"/>
                    <select id="select_antibiotic" ref="select_antibiotic" value={this.state.antibiotic} defaultValue={this.state.antibiotics[0]._links.self.href} >
                        {antibiotics}
                    </select>

                    <input type="text" ref="update_dosage2" value={this.state.dosage} placeholder="" className="form-control" id="dosage"/>
                    <input type="text" ref="update_multiplicity2" value={this.state.multiplicity} placeholder="" className="form-control" id="multiplicity"/>

                    <button onClick={this.create} className="btn btn-outline-dark"></button>
                    <button onClick={this.changeShow} className="btn btn-outline-dark"></button>

                </form>
            </div>
        )

    }

    onChangeSt (date) {
        let d = date;

        let a = d.getUTCDate() + 1;
        let b = d.getUTCMonth() + 1;
        let c = d.getUTCFullYear();

        document.getElementById('updateDateSt').value=a + "." + b + "." + c;
        this.setState({
            showStateSt: false
        });

    }

    showSt () {
        this.setState({
            showStateSt: !this.state.showStateSt

        });
    }

    changeRendUp() {
        this.setState({
            startcomment: this.refs.update_startcomment2.value,
            antibiotic: this.refs.select_antibiotic.value,
            dosage: this.refs.update_dosage2.value,
            multiplicity: this.refs.update_multiplicity2.value
        });
    }

    renderUpSt() {

        let antibiotics = this.state.antibiotics.map(antibiotic =>
            <option value = {antibiotic._links.self.href}>{antibiotic.name}</option>
        );


        return (
            <div className="col-6">

                <form ref="myForm" onChange={this.changeRendUp}>
                    <input type="text" ref="update_startdate2" onClick={this.showSt} placeholder=" " className="form-control" id="updateDateSt"/>
                    <Calendar onChange={this.onChangeSt} />
                    <input type="text" ref="update_startcomment2" value={this.state.startcomment} placeholder="  " className="form-control" id="startcomment"/>
                        <select id="select_antibiotic" ref="select_antibiotic" value={this.state.antibiotic} >
                            {antibiotics}
                            </select>

                    <input type="text" ref="update_dosage2" value={this.state.dosage} placeholder="" className="form-control" id="dosage"/>
                    <input type="text" ref="update_multiplicity2" value={this.state.multiplicity} placeholder="" className="form-control" id="multiplicity"/>

                    <button onClick={this.create} className="btn btn-outline-dark"></button>
                    <button onClick={this.changeShow} className="btn btn-outline-dark"></button>
                </form>
            </div>
        )

    }

    renderNull() {
        return (
            <div>
                <button onClick={this.changeShow} className="btn btn-outline-dark"> </button>
            </div>
        )
    }

    render() {

        if (this.state.showOnCreate && this.state.showStateSt ){
            return this.renderUpSt();
        }

        else if (this.state.showOnCreate) {

            return this.renderUp();
        } else {
            return this.renderNull();
        }
    }



}

class UpdatePrescribtion extends React.Component{
    constructor(props) {
        super(props);
        this.Update = this.Update.bind(this);
        this.changeRendUp = this.changeRendUp.bind(this);
        this.renderUp = this.renderUp.bind(this);
        this.renderUpEn = this.renderUpEn.bind(this);
        this.renderNull = this.renderNull.bind(this);
        this.onChangeEn = this.onChangeEn.bind(this);
        this.showEn = this.showEn.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.state = { showStateEn: false, endcomment: []};

    }


    Update(e) {
        e.preventDefault();
        let enddate = this.refs.update_enddate2_.value;
        let endcomment = this.refs.update_endcomment2.value;
        this.props.onSaveUp(enddate, endcomment, this.props.prescribtionUp);
        this.setState({
            endcomment: ""
        });

    }

    handleClose() {
        this.props.onClose();
        this.setState({
            endcomment: ""
        });
    }

    renderUp() {

        return (
            <div className="col-6">

                <form ref="myForm" onChange={this.changeRendUp}>

                    <input type="text" ref="update_enddate2_" onClick={this.showEn} placeholder=" " className="form-control" id="updateDateEn_2" />
                    <input type="text" ref="update_endcomment2" value={this.state.endcomment} placeholder="  " className="form-control" id="endcomment"/>
                    <button onClick={this.Update} className="btn btn-outline-dark"></button>
                    <button onClick={this.handleClose} className="btn btn-outline-dark"></button>

                </form>
            </div>
        )

    }



    onChangeEn (date) {
        var d = date;

        let a = d.getUTCDate() + 1;
        let b = d.getUTCMonth() + 1;
        let c = d.getUTCFullYear();

        document.getElementById('updateDateEn_2').value=a + "." + b + "." + c;

        this.setState({
            showStateEn: false

        });

        }

    changeRendUp() {
        this.setState({
            endcomment: this.refs.update_endcomment2.value
        });
    }

    showEn () {
        this.setState({
            showStateEn: !this.state.showStateEn
        });
    }

    renderUpEn() {


        return (
            <div className="col-6">
                <form ref="myForm" onChange={this.changeRendUp}>
                    <input type="text" ref="update_enddate2_" onClick={this.showEn} placeholder=" " className="form-control" id="updateDateEn_2" />
                    <Calendar onChange={this.onChangeEn} />
                    <input type="text" ref="update_endcomment2" value={this.state.endcomment} placeholder="  " className="form-control" id="endcomment"/>
                    <button onClick={this.Update} className="btn btn-outline-dark"></button>
                    <button onClick={this.handleClose} className="btn btn-outline-dark"></button>
                </form>
            </div>
        )

    }

    renderNull() {
        return (
            <div>

            </div>
        )
    }

    render() {


        if (this.props.showOnUpdate && this.state.showStateEn) {
            return this.renderUpEn();
        } else if(this.props.showOnUpdate){
            return this.renderUp();
        } else  {
            return this.renderNull();
        }
    }
}



class Prescribtion extends React.Component{

    constructor(props) {
        super(props);
        this.handleDelete = this.handleDelete.bind(this);
        this.handleUpdate = this.handleUpdate.bind(this);
        this.handleOpen = this.handleOpen.bind(this);
        this.rendUp = this.rendUp.bind(this);
        this.rendNorm = this.rendNorm.bind(this);
    }

    handleDelete() {
        this.props.onDelete(this.props.prescribtion);
    }

    handleUpdate() {
        this.props.onUpdate(this.props.prescribtion);
    }

    handleOpen() {
        this.props.onOpen(this.props.prescribtion);
    }



    rendUp() {
        return (

            <div>
                <tr className="d-flex">
                    <td className="col-1">{this.props.prescribtion.startdate}</td>
                    <td className="col-1">{this.props.prescribtion.startcomment}</td>
                    <td className="col-1">{this.props.prescribtion.doctorSurname}</td>
                    <td className="col-1">{this.props.prescribtion.doctorSpecialtyName}</td>

                    <td className="col-1">{this.props.prescribtion.name} </td>
                    <td className="col-1">{this.props.prescribtion.dosage} {this.props.prescribtion.multiplicity}</td>


                    <td className="col-1"><p></p></td>
                    <td className="col-1"><p></p></td>
                    <td className="col-1"><p></p></td>
                    <td className="col-1"><p></p></td>

                    <td className="col-1"><button onClick={this.handleDelete} className="btn btn-outline-dark"></button></td>
                    <td className="col-1"><button onClick={this.handleUpdate} className="btn btn-outline-dark"></button></td>
                </tr>
            </div>
        )
    }

    rendNorm() {
        return (



            <div>
                <tr className="d-flex">
                    <td className="col-1">{this.props.prescribtion.startdate}</td>
                    <td className="col-1">{this.props.prescribtion.startcomment}</td>
                    <td className="col-1">{this.props.prescribtion.doctorSurname}</td>
                    <td className="col-1">{this.props.prescribtion.doctorSpecialtyName}</td>

                    <td className="col-1">{this.props.prescribtion.name} </td>
                    <td className="col-1"> {this.props.prescribtion.dosage} {this.props.prescribtion.multiplicity}</td>

                    <td className="col-1">{this.props.prescribtion.enddate}</td>
                    <td className="col-1">{this.props.prescribtion.endcomment}</td>
                    <td className="col-1">{this.props.prescribtion.doctor2Surname}</td>
                    <td className="col-1">{this.props.prescribtion.doctor2SpecialtyName}</td>

                    <td className="col-1"><button onClick={this.handleDelete} className="btn btn-outline-dark"></button></td>
                </tr>
            </div>
        )
    }

    render() {
        if (this.props.prescribtion.b == false) {
            return this.rendNorm();
        } else {
            return this.rendUp();
        }
    }
}


export default FindPrescribtion;
